<template>
    <Collapse v-model="value1">
        <Panel v-for="i in navData">
            {{navData[''+i]}}
            <Table slot="content"  :data="navData[''+i]"></Table>
        </Panel>
    </Collapse>
</template>

<script>
  export default {
    name: 'nav.vue',
    data:()=>{
      return{
        value1:'1',
        navData:{}
      }
    },
    methods:{},
    created () {
      this.$http.post("/console/nav/get").then(function (data){
        this.navData=JSON.parse(data.body);
      },function (err) {
        this.$Modal.error({
          title: '警告',
          content: '服务器发生错误'
        })
      });
    }
  }
</script>

